@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
	*,
	*::before,
	*::after {
		box-sizing: border-box;
		margin: 0;
		padding: 0;
	}
	html {
		scroll-behavior: smooth;
	}

	.sidebar-icon {
		@apply relative flex items-center justify-center h-12 w-12 mx-auto mt-8
        dark:bg-gray-800 dark:text-green-500 bg-gray-200 text-green-600
        hover:bg-green-600 hover:text-white dark:hover:bg-gray-200
        rounded-3xl hover:rounded-xl
        transition-all 
        duration-300 ease-linear
        cursor-pointer;
	}

	.sidebar-tooltip {
		@apply absolute w-auto p-2 m-2 min-w-max left-14
        rounded-md shadow-md 
        dark:text-white dark:bg-gray-900 text-black bg-gray-200
        text-xs font-bold
        transition-all duration-100 scale-0 origin-left;
	}

	.sidebar-hr {
		@apply dark:bg-gray-200 bg-gray-800 
        border dark:border-gray-200 border-gray-800 rounded-full
        mx-2 mt-5;
	}

	.darkmode {
		@apply mb-auto;
	}

	.home-page {
		@apply ml-16 dark:bg-gray-900 bg-gray-100 dark:text-gray-200 text-black
        flex flex-col justify-center items-center;
	}

	.profile-img {
		@apply w-auto lg:h-screen md:h-2/3 h-1/2
        rounded-sm;
	}

	.home {
		@apply mr-40 -ml-10;
	}

	.home-name {
		@apply md:text-6xl text-lg font-bold;
	}

	.home-title {
		@apply font-bold text-xl mt-10 dark:text-emerald-300 text-emerald-600 animate-pulse;
	}

	.about-page {
		@apply ml-16 dark:bg-gray-900 bg-gray-100 dark:text-gray-200 text-black
        h-screen pt-10;
	}

	.about-title {
		@apply font-bold text-2xl text-center mb-20 pt-36;
	}

	.about {
		@apply flex flex-col justify-center items-center h-1/2;
	}

	.about-img {
		@apply w-1/4 h-auto rounded-full float-right;
	}

	.about-infos {
		@apply mb-5;
	}

	.about-subtitle {
		@apply dark:text-sky-300 text-sky-600;
	}

	.skills-page {
		@apply ml-16 dark:bg-gray-900 bg-gray-100 dark:text-gray-200 text-black
        flex flex-col items-center h-screen pt-10 pl-16;
	}

	.skills-title {
		@apply font-bold text-2xl text-center mr-16 mb-10;
	}

	.skills-subtitle {
		@apply underline hover:animate-pulse;
	}

	.lang-icon {
		@apply w-10 h-10 inline-block align-middle mr-2 mb-2 hover:animate-spin;
	}

	.project-page {
		@apply dark:bg-gray-900 bg-gray-100 dark:text-gray-200 text-black
        flex flex-col items-center h-screen pt-10 pl-16;
	}

	.project-title {
		@apply font-bold text-2xl text-center mb-5;
	}

	.project-img {
		@apply w-1/4 h-2/5 align-top;
	}

	.project {
		@apply ml-1 relative;
	}

	.project-list {
		@apply h-4/5;
	}

	.project-icon {
		@apply absolute right-0 bottom-0 dark:bg-gray-200 bg-red-200 rounded-md hover:rounded-xl transition-all duration-300 ease-linear;
	}

	.viewmore {
		@apply ml-auto mr-5 mb-0 px-2 bg-gray-700 dark:bg-gray-200 rounded-3xl
		dark:text-black text-white hover:rounded-sm dark:hover:text-red-400 hover:text-red-200 hover:bg-gray-800
        transition-all duration-300 ease-linear;
	}

	.tag {
		@apply inline-block dark:bg-gray-200 bg-gray-800 rounded-full px-3 py-1
		 text-sm font-semibold dark:text-gray-700 text-gray-200 mr-2 mb-2;
	}

	.contact-page {
		@apply ml-16 dark:bg-gray-900 bg-gray-100 dark:text-gray-200 text-black
		flex flex-col items-center h-screen;
	}

	.contact-details {
		@apply ml-5;
	}

	.contact-title {
		@apply font-bold text-2xl text-center mb-36 mt-10;
	}

	.contact-divider {
		@apply w-full p-0.5 bg-green-600 dark:bg-blue-600 lg:w-1/3 mt-3 mb-3;
	}

	.contact-icon {
		@apply w-10 h-10 mb-2;
	}

	.social-icons {
		@apply mt-10;
	}
	.social-icon {
		@apply w-12 h-12 mx-2 dark:bg-gray-200 bg-red-200 rounded-3xl
		hover:text-white hover:rounded-xl
        transition-all duration-300 ease-linear;
	}

	.dark-box {
		-webkit-box-shadow: 0px 3px 0px 0px #e04343;
		-moz-box-shadow: 0px 3px 0px 0px #e04343;
		box-shadow: 0px 3px 0px 0px #e04343;
		padding: 20px 0;
		@apply dark:bg-gray-600 bg-gray-200 w-11/12
		flex flex-col items-center justify-center
		transition-all duration-300 ease-linear mr-36 mb-10;
	}
	.dark-box:hover,
	.dark-box:focus {
		-webkit-box-shadow: 0px 3px 0px 0px #ffe801;
		-moz-box-shadow: 0px 3px 0px 0px #ffe801;
		box-shadow: 0px 3px 0px 0px #ffe801;
	}
	.box-hover {
		transition: all 500ms ease-in-out;
		-webkit-transition: all 500ms ease-in-out;
		-moz-transition: all 500ms ease-in-out;
		-o-transition: all 500ms ease-in-out;
	}
	.box-hover:hover,
	.box-hover:focus {
		transform: translate(0, -15px);
		-webkit-transform: translate(0, -15px);
		-ms-transform: translate(0, -15px);
	}
}
